<template>
  <div class="findmusic">
    <div class="find_menu">
      <router-link class="menu_list" :to="{name: 'finderecommend'}" tag="div">个性推荐</router-link>
      <router-link class="menu_list" :to="{name: 'findsheet'}" tag="div">歌单</router-link>
      <router-link class="menu_list" :to="{name: 'radiostation'}" tag="div">主播电台</router-link>
      <router-link class="menu_list" :to="{name: 'rankinglist'}" tag="div">排行榜</router-link>
      <!-- <div class="hr"></div> -->
    </div>

    <transition :name="$store.state.routeDir" mode="out-in">
      <router-view></router-view>
    </transition>
  </div>
</template>

<script>
export default {

}
</script>

<style lang="scss" scoped>
@import '../../assets/css/global.scss';

.findmusic {
  position: absolute;
  top: 50px;
  bottom: 50px;
  left: 0;
  right: 0;
  background-color: #f3f4f6;
}
.find_menu {
  position: relative;
  background-color: #fff;
  display: flex;
  height: 36px;
  .menu_list {
    flex: 1;
    text-align: center;
    line-height: 36px;
    &.active { border-bottom: 2px solid $primarycolor; }
    &:active { background-color: $list_active; }
  }
  .hr {
    position: absolute;
    bottom: 0;
    height: 2px;
    background-color: $primarycolor;
    width: 70px;
  }
}

</style>



