

<template>
  <div class="finderecommend">
    <swiper :options="swiperOption">
      <swiper-slide v-for="(item, index) in recommendImageList" :key="index">
        <img :src="item.src" class="img">
        <span class="type" :style="{ backgroundColor: item.color }">{{ item.type }}</span>
      </swiper-slide>

      <div class="swiper-pagination"  slot="pagination"></div>
    </swiper>

    <ul class="recommend_type">
      <li class="item">
        <i class="ico icon-fm"></i>
        <p class="title">私人FM</p>
      </li>
      <li class="item">
        <i class="ico icon-date"></i>
        <p class="title">每日听歌推荐</p>
      </li>
      <li class="item">
        <i class="ico icon-rank-list"></i>
        <p class="title">云音乐热歌榜</p>
      </li>
    </ul>

    <!-- 推荐歌单 -->
    <findsheetlist class="recommend_list" title="推荐歌单"></findsheetlist>
  </div>
</template>

<script>
import { swiper, swiperSlide } from "vue-awesome-swiper";
import findsheetlist from './findsheetlist'

export default {
  data() {
    return {
      swiperOption: {
        autoplay: 5000,
        loop: true,
        pagination: ".swiper-pagination",
        paginationClickable: true,
        preloadImages: false,
        lazyLoading : true
      }
    };
  },
  computed: {
    // swiper轮播图的数据
    recommendImageList() {
      return this.$store.getters.recommendImageList;
    }
  },
  components: {
    swiper,
    swiperSlide,
    findsheetlist
  }
};
</script>

<style lang="scss" scoped>
@import '../../assets/css/global.scss';

.finderecommend {
  position: absolute;
  top: 36px;
  bottom: 0;
  left: 0;
  right: 0;
  overflow-x: hidden;
  -webkit-overflow-scrolling: touch;
  .swiper-container {
    height: calc(100vw * (280 / 640));
    .swiper-slide {
      position: relative;
    }
    .type {
      position: absolute;
      bottom: 20px;
      right: 0;
      padding: 5px 12px;
      color: #fff;
      border-radius: 12px 0 0 12px;
      line-height: 1;
      vertical-align: middle;
    }
    img {
      width: 100%;
    }
    .swiper-pagination-bullet {
      background: rgb(255, 255, 255);
      opacity: 0.4;
    }
    .swiper-pagination-bullet-active {
      background: rgba(198, 47, 47, 0.8);
      opacity: 1;
    }
    .swiper-pagination-bullets { bottom: 5px; }
  }

  .recommend_type {
    display: flex;
    text-align: center;
    .item {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      flex: 1;
      height: 90px;
    }
    .ico {
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 32px;
      color: $primarycolor;
      width: 50px;
      height: 50px;
      border-radius: 50%;
      border: 1px solid $primarycolor;
    }
    .title {
      font-size: 12px;
      line-height: 1;
      margin-top: 8px;
    }
  }
}
</style>



