<template>
  <div class="findsheetlist">
    <findsheettitle :title="title"></findsheettitle>

    <ul class="content clearfix">
      <li v-for="(item, index) in recommendMinSheets" :key="index">
        <img v-lazy="item.info[0].img_url" alt="">
        <p class="info">
          {{ item.bottomtitle }}
        </p>
      </li>
    </ul>
  </div>
</template>

<script>
import findsheettitle from './findsheettitle'

export default {
  props: {
    title: String
  },
  computed: {
    recommendMinSheets() {
      return this.$store.getters.recommendMinSheets
    }
  },
  components: {
    findsheettitle
  }
}
</script>

<style lang="scss" scoped>
@import "../../assets/css/global.scss";

.findsheetlist { font-size: 12px;
  li { width: calc((100vw - 4px) / 3); float: left; margin-bottom: 8px;
    img { width: 100%; height: calc((100vw - 4px) / 3); }
    .info { height: 34px; overflow: hidden; margin: 8px 3px; line-height: 17px; }
    &:nth-child(3n+2) { padding: 0 2px; box-sizing: content-box; }
  }
}
</style>



