<template>
  <header class="title">
    <span class="name">{{ title }}</span>
    <i class="ico icon-right"></i>
  </header>
</template>

<script>
export default {
  props: {
    title: {
      type: String,
      default: '标题'
    }
  }
}
</script>

<style lang="scss" scoped>
@import "../../assets/css/global.scss";

.title {
  height: 40px;
  line-height: 40px;
  font-size: 16px;
  .name {
    height: 18px;
    vertical-align: middle;
    line-height: 1;
    padding-left: 9px;
    border-left: 3px solid $primarycolor;
  }
  .ico {
    color: #999;
    vertical-align: middle;
  }
}
</style>



